export class VideoPlayer {
  constructor(videoSelector, btnSelector) {
    this.video = document.querySelector(videoSelector);
    this.playBtn = document.querySelector(btnSelector);
    this.isPlay = false;

    this.init();
  }

  init() {
    this.on();
  }

  on() {
    this.playBtn.addEventListener("click", () => this.playVideo());
    this.video.addEventListener("click", () => this.togglePlay());
    this.video.addEventListener("play", () => this.handlePlay());
    this.video.addEventListener("pause", () => this.handlePause());
  }

  playVideo() {
    this.video.play();
    this.activateControls();
  }

  togglePlay() {
    this.video.paused ? this.video.play() : this.video.pause();
    this.activateControls();
  }

  handlePlay() {
    this.video.classList.add("talking__media-video--play");
    this.playBtn.classList.add("talking__media-btn--play");
    this.activateControls();
  }

  handlePause() {
    this.playBtn.classList.remove("talking__media-btn--play");
  }

  activateControls() {
    if (this.isPlay) return;

    this.isPlay = true;
    this.video.controls = true;
  }
}
