export class Map {
  constructor(target, apiKey) {
    this.mapContainer = document.getElementById(target);
    this.apiKey = apiKey;

    // Base map configuration
    this.mapConfig = {
      center: [55.807739, 37.757853],
      zoom: 12, // Reduced zoom to fit more markers
      controls: ["zoomControl"], // Removed commented fullscreenControl for clarity
    };

    // Array for balloon texts/content (60 random entries)
    this.balloonContents = this.generateBalloonContents(60);

    // Generated random positions around the center
    this.markerPositions = this.generateRandomPositions(
      60,
      this.mapConfig.center,
      0.1
    ); // Spread within ~5km radius approx.

    this.isLoaded = false;
    this.observer = null;

    this.init();
  }

  // Helper to generate random balloon contents
  generateBalloonContents(count) {
    const contents = [];
    const baseAddresses = [
      "ул. Ленина",
      "пр. Мира",
      "ул. Советская",
      "ш. Энтузиастов",
      "ул. Пушкина",
      "ул. Гагарина",
      "пер. Садовый",
      "ул. Кирова",
      "бул. Цветной",
      "ул. Московская",
    ];
    const baseHours = [
      "09:00–21:00",
      "10:00–22:00",
      "08:00–20:00",
      "круглосуточно",
    ];
    const ratings = [4.0, 4.2, 4.5, 4.7, 4.8, 5.0];

    for (let i = 1; i <= count; i++) {
      const randomAddressIndex = Math.floor(
        Math.random() * baseAddresses.length
      );
      const randomHoursIndex = Math.floor(Math.random() * baseHours.length);
      const randomRating = ratings[Math.floor(Math.random() * ratings.length)];
      const randomPhone = `+7 (495) ${Math.floor(
        100 + Math.random() * 900
      )}-${Math.floor(10 + Math.random() * 90)}-${Math.floor(
        10 + Math.random() * 90
      )}`;

      contents.push({
        balloonHeader: `Клиника №${i}`,
        rating: randomRating.toFixed(1),
        address: `${baseAddresses[randomAddressIndex]}, ${Math.floor(
          1 + Math.random() * 100
        )}, Москва`,
        hours: `Ежедневно: ${baseHours[randomHoursIndex]}`,
        phone: randomPhone,
      });
    }
    return contents;
  }

  // Helper to generate random positions around a center
  generateRandomPositions(count, center, spread) {
    const positions = [];
    for (let i = 0; i < count; i++) {
      const latOffset = (Math.random() - 0.5) * spread;
      const lonOffset = (Math.random() - 0.5) * spread;
      positions.push([center[0] + latOffset, center[1] + lonOffset]);
    }
    return positions;
  }

  init() {
    if (!this.mapContainer) return;
    this.setupObserver();
  }

  setupObserver() {
    this.observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && !this.isLoaded) {
            this.isLoaded = true;
            this.loadYandexMap();
            this.observer.unobserve(this.mapContainer);
          }
        });
      },
      { rootMargin: "200px" }
    );

    this.observer.observe(this.mapContainer);
  }

  loadYandexMap() {
    const loader = this.mapContainer.querySelector(".location-map__loader");
    if (loader) loader.classList.add("location-map__loader--active");

    const script = document.createElement("script");
    script.src = `https://api-maps.yandex.ru/2.1/?apikey=${this.apiKey}&lang=ru_RU`;
    script.async = true;

    script.onload = () => {
      ymaps.ready(this.createMap.bind(this));
      if (loader) loader.style.display = "none";
    };

    document.head.appendChild(script);
  }

  createMap() {
    // Get responsive config based on screen width
    const config = this.getResponsiveConfig();

    // Custom balloon layout
    const BalloonContentLayout = ymaps.templateLayoutFactory.createClass(
      '<div class="custom-balloon">' +
        '<div class="custom-balloon__title">$[properties.balloonHeader]</div>' +
        '<div class="custom-balloon__rating">' +
        '<span class="custom-balloon__rating-value">$[properties.rating]</span> ' +
        '<span class="custom-balloon__stars">☆☆☆☆☆<span>★★★★★</span></span>' +
        "</div>" +
        '<div class="custom-balloon__details">' +
        '<div class="custom-balloon__address">Адрес<strong>$[properties.address]</strong></div>' +
        '<div class="custom-balloon__hours">Время работы<strong>$[properties.hours]</strong></div>' +
        '<div class="custom-balloon__phone">Телефон<strong>$[properties.phone]</strong></div>' +
        "</div>" +
        "</div>"
    );

    // Create the map
    const myMap = new ymaps.Map("map", {
      center: config.mapCenter,
      zoom: this.mapConfig.zoom,
      controls: this.mapConfig.controls,
    });

    // Disable unnecessary behaviors
    myMap.behaviors.disable([
      "dblClickZoom",
      "multiTouch",
      "rightMouseButtonMagnifier",
      "leftMouseButtonMagnifier",
    ]);

    // Add 60 placemarks
    this.markerPositions.forEach((position, index) => {
      const placemark = new ymaps.Placemark(
        position,
        this.balloonContents[index],
        {
          iconLayout: "default#image",
          iconImageHref: "./images/common/mdi_location.svg",
          iconImageSize: [48, 64],
          iconImageOffset: [-24, -64],
          iconShape: {
            type: "Rectangle",
            coordinates: [
              [-24, -64],
              [24, 0],
            ],
          },
          balloonContentLayout: BalloonContentLayout,
          balloonOffset: config.balloonOffset,
          balloonMinWidth: config.balloonWidth,
          balloonMinHeight: config.balloonHeight,
        }
      );

      myMap.geoObjects.add(placemark);
    });

    // Apply map pixel offset if needed
    if (config.mapPixelOffset[0] !== 0 || config.mapPixelOffset[1] !== 0) {
      const pixelCenter = myMap.getGlobalPixelCenter();
      myMap.setGlobalPixelCenter([
        pixelCenter[0] + config.mapPixelOffset[0],
        pixelCenter[1] + config.mapPixelOffset[1],
      ]);
    }
  }

  // Responsive configuration based on screen width
  getResponsiveConfig() {
    const screenWidth = window.innerWidth;

    if (screenWidth < 768) {
      // Mobile
      return {
        mapCenter: this.mapConfig.center,
        balloonOffset: [0, -10],
        balloonWidth: 290,
        balloonHeight: 280,
        mapPixelOffset: [0, 160],
      };
    } else if (screenWidth < 1024) {
      // Tablet
      return {
        mapCenter: this.mapConfig.center,
        balloonOffset: [0, -10],
        balloonWidth: 250,
        balloonHeight: 280,
        mapPixelOffset: [100, -80],
      };
    } else {
      // Desktop
      return {
        mapCenter: this.mapConfig.center,
        balloonOffset: [0, -10],
        balloonWidth: 300,
        balloonHeight: 300,
        mapPixelOffset: [150, -160],
      };
    }
  }
}
