export class ScrollNav {
  constructor() {
    this.SECTIONS = document.querySelectorAll(
      "section[id], header[id], footer[id]"
    );
    this.NAV_BTN = document.querySelector(".scroll-nav__btn");
    this.NAV_ITEMS = document.querySelectorAll(".scroll-nav a");

    this.navItemsMap = new Map();
    this.observer = null;
    this.currentSectionId = "";

    this.init();
  }

  init() {
    this.observer = new IntersectionObserver(this.intersection.bind(this), {
      root: null,
      threshold: 0.3,
      rootMargin: "0px",
    });

    this.on();
  }

  on() {
    this.NAV_BTN.addEventListener("click", () => this.dropdownToggle());

    document.addEventListener("click", (e) => {
      if (!e.target.closest(".scroll-nav")) {
        this.NAV_BTN.classList.remove("scroll-nav__btn--open");
      }
    });

    this.NAV_ITEMS.forEach((link) => {
      this.navItemsMap.set(link.getAttribute("href").slice(3), link);

      link.addEventListener("click", (e) => {
        this.dropdownPick(e);
        this.dropdownToggle();
      });
    });

    this.SECTIONS.forEach((section) => {
      this.observer.observe(section);
    });
  }

  intersection(entries) {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        this.NAV_ITEMS.forEach((link) => {
          this.removeActiveClass(link);
        });

        const link = this.navItemsMap.get(entry.target.id);

        this.addActiveClass(link);
        this.NAV_BTN.textContent = link.firstChild.textContent;
      }
    });
  }

  addActiveClass(link) {
    link.classList.add("active");
    link.setAttribute("aria-current", true);
  }

  removeActiveClass(link) {
    link.classList.remove("active");
    link.setAttribute("aria-current", false);
  }

  dropdownToggle() {
    this.NAV_BTN.classList.toggle("scroll-nav__btn--open");
  }

  dropdownPick(e) {
    this.NAV_BTN.textContent = e.target.textContent;
  }
}
